.modal {
  display: none;
  position: fixed;
  z-index: 1;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgba(0, 0, 0, 0.6);
  z-index: 1000;
}
.modal .modal_content {
  background-color: #fefefe;
  margin: 15% auto;
  // padding: 20px;
  border: 1px solid #888;
  width: 80%;
  z-index: 99999;
}

.modal .modal_content .close_modal_window {
  color: #000000;
  float: right;
  font-size: 35px;
  font-weight: normal;
  cursor: pointer;
  margin-top: 5px;
  margin-bottom: 5px;
  margin-left: auto;
}

li {
  list-style: none;
}

.modal__container {
  padding-left: 20px;
  padding-right: 20px;
  max-width: 280px;
  max-height: 958px;
}

.modal__card__title {
  margin-bottom: 20px;
  margin-top: 30px;
  @include font(500, 20px, 1.15);
}

.card__modal__item {
  &:not(:last-child) {
    margin-bottom: 10px;
  }
  @include font(500, 12px, 1.33);
  color: #8c8c8c;
}

.card__title_info {
  margin-top: 20px;
  margin-bottom: 10px;
  @include font(500, 12px, 1.33);
}

.card__modal__item__text {
  margin-bottom: 21px;
  @include font(500, 12px, 1.67);
}

.container__card {
  display: flex;
}

.card__modal__list_rigth {
  margin-right: 41px;
  width: 68px;
}

.color__vote {
  padding: 1px 10px 1px 10px;
  background: #ff6b01;
  border-radius: 5px;
  color: #ffffff;
}

.color__votes {
  padding: 1px 4px 1px 4px;
  background-color: #f7f7f7;
  border-radius: 5px;
  color: #000000;
}

.change__background {
  padding: 1px 4px 1px 4px;
  background: #f7f7f7;
  border-radius: 5px;
}

.watched {
  width: 112px;
  height: 44px;
  background: white;
  border-radius: 5px;
  border: 1px solid #000000;
  margin-right: 15px;
  @include font(500, 12px, 1.33);

  &:hover,
  &:focus {
    background-color: #ff6b01;
    color: white;
  }
}

.queue {
  width: 112px;
  height: 44px;
  background: white;
  border-radius: 5px;
  border: 1px solid #000000;
  @include font(500, 12px, 1.33);

  &:hover,
  &:focus {
    background-color: #ff6b01;
    color: white;
  }
}

.container__btn {
  display: flex;
  justify-content: start;
  align-items: center;
  padding-bottom: 40px;
}

.card_modal_item_more {
  // max-width: 240px;
  // min-height: 236px;
  @include font(500, 12px, 1.66);
  font-style: normal;
  font-family: 'Roboto', sans-serif;
}

.card__modal__item--change__color {
  color: #000000;
}

@media screen and (min-width: 768px) {
  .modal__container {
    padding-left: 28px;
    padding-right: 30px;
    max-width: 618px;
    max-height: 562px;
  }

  .container__info {
    display: flex;
    padding-top: 42px;
    max-width: 618px;
    max-height: 562px;
  }

  .img__film {
    width: 264px;
    height: 373px;
    //    padding-right: 32px;
  }

  .modal .modal_content .close_modal_window {
    margin-top: 5px;
    margin-right: -20px;
  }

  // .card_modal_item_more {
  //   // max-width: 264px;
  //   // height: 236px;
  // }

  .modal__card__title {
    margin-top: 0px;
  }

  .watched {
    width: 125px;
    height: 44px;
  }

  .queue {
    width: 125px;
    height: 44px;
  }

  .container__btn {
    margin-top: 0px;
  }

  .container__tablet {
    margin-left: 32px;
  }
}

@media screen and (min-width: 1024px) {
  .modal__container {
    padding-left: 30px;
    padding-right: 30px;
    max-width: 882px;
    max-height: 568px;
  }

  .container__info {
    display: flex;
    padding-top: 50px;
    max-width: 882px;
    max-height: 568px;
  }

  .img__film {
    width: 396px;
    height: 478px;
    margin-bottom: 40px;
    //    padding-right: 32px;
  }

  .modal .modal_content .close_modal_window {
    margin-top: 5px;
    margin-right: -20px;
  }

  // .card_modal_item_more {
  //   // max-width: 396px;
  //   // height: 236px;
  // }

  .modal__card__title {
    margin-top: 0px;
  }

  .watched {
    width: 136px;
    height: 44px;
  }

  .queue {
    width: 136px;
    height: 44px;
  }

  .container__btn {
    margin-top: 0px;
  }

  .container__tablet {
    margin-left: 32px;
  }
}

